const Container = ({ children }) => {
  return <div style={{ width: '800px', maxWidth: '100%' }}>{children}</div>;
};

const Test = ({ color, children }) => {
  return (
    <div
      style={{
        backgroundColor: color,
        color: 'white',
        display: 'grid',
        justifyContent: 'center',
        alignContent: 'center',
      }}
    >
      {children}
    </div>
  );
};

